<template>
    <div class="com-container">
        <div class="com-chart" ref="line_ref">
        </div>
    </div>
</template>

<script>
import * as echarts from 'echarts'
import 'echarts-gl'
import $ from 'jquery'
import axios from 'axios'
export default {
  data () {
    return {
      // myChart: null
    }
  },
  // 开始生命周期
  mounted () {
    this.initChart()
    this.getData()
    this.updateChart()
  },
  // 放所有所需函数
  methods: {
    initChart () {
      // this.myChart = this.$echarts.init(this.$refs.line_ref, 'dark')
    },
    getData () {
      console.log(1)
    },
    updateChart () {
      const myChart = this.$echarts.init(this.$refs.line_ref)
      axios.get ('http://localhost:8080/data/MOP.json').then(data =>{
        myChart.setOption({
          grid3D: {},
          xAxis3D: {},
          yAxis3D: {},
          zAxis3D: {},
          dataset: {
            dimensions: [
              'length',
              'bifur_num',
              'mean_sum',
              'std_sum',
              'mean_std',
              'std_std',
              {name: 'name', type: 'ordinal'}
            ],
            source: data
          },
          series: [{
            type: 'scatter3D',
            symbolSize: 1,
            encode: {
              x: 'std_sum',
              y: 'bifur_num',
              z: 'mean_std',
              // tooltip: [0, 1, 2, 3, 4]
            }
          }]
        })
        // myChart.setOption(option)
      })
      console.log(2)
    }
  }
}
</script>

