import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import ScreenPage from '../views/ScreenPage.vue'
import LinePage from '../views/LinePage.vue'
import Scatter3dPage from '../views/Scatter3dPage.vue'
import Scatter3dPage_dataset from '../views/Scatter3dPage_dataset.vue'
import Sunburst from '../views/SunburstPage.vue'
import Bar3d from '../views/Bar3dPage.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  { // 重定向到主页
    path: '/',
    redirect: 'screen'
  },
  { // 主页
    path: '/screen',
    component: ScreenPage,
  },
  { path: '/line', component: LinePage },
  { path: '/scatter3d', component: Scatter3dPage },
  { path: '/scatter3d_dataset', component: Scatter3dPage_dataset },
  { path: '/sunburst', component: Sunburst },
  { path: '/bar3d', component: Bar3d }
]

const router = new VueRouter({
  routes
})

export default router
